<template>
  <div class='wrapper'>
    <h1 class="divided">装备图鉴</h1>
    <go-back />
    <div v-if="loading">
        <load-indicator loadText="正在加载中"></load-indicator>
    </div>
    <div style="margin: 10px 0">
         职业
        <select @change="watchChange" v-model="selectJob">
            <option value="" selected disabled hidden></option>
            <option v-for="(f,k) in jobs" :key="k" :value ="f.RowId">{{f.name}}</option>
        </select>
    </div>
    <tabs ref='eqbook' mode="verticle" @clickTab="getTabInfo" :defaultTabId="defaultTab">
        <tab class="tab" v-for="(item,index) in category" :key="index" :title="item.name" :id="item._TabID">
            <div class="item-grid">
                <div class="grid-entry" v-for="(item,index) in GridTotal" :key="index">
                    <template v-if="item">
                        <div @mouseenter="onHoverIn(item.RowId)" @mouseleave="onHoverOut">
                            <item-icon-tooltip
                                :itemId="item._ItemID" 
                                :itemStub="item.item"
                                :count="1"
                                :noLink="noLink"
                                :directionHint="index > 36 ? 'up' : 'down'">
                            </item-icon-tooltip>
                        </div>
                    </template>
                </div>
            </div>
        </tab>
    </tabs>
    <div class="rightPannel" v-if="isHover && rowcontent[currentRowId]">
        <p>获取难度:{{rowcontent[currentRowId]._GetLevel}}</p>
        <p>描述：<span v-html="rowcontent[currentRowId].desc"></span></p>
        <p>获取途径：<span v-for="(v,k) in rowcontent[currentRowId].gain" :key="k">{{v}}</span></p>
    </div>
    <back-to-top visibleoffset="300"><span class="topButton"><i class="fa fa-arrow-up"></i></span></back-to-top>
  </div>
</template>

<script>
import Vue from 'vue';
import Tab from "@/components/Tab/Tab";
import Tabs from "@/components/Tab/Tabs";
import GameInfo from '../api/GameInfo';
import Loader from "@/components/util/Loader";
import GoBack from "@/components/util/GoBack.vue";
import ItemIconTooltip from '@/old/items/ItemIconTooltip.vue';
import BackToTop from '@/components/util/BacktoTop';
import { debounce } from 'lodash';
Vue.component("load-indicator", Loader);
Vue.component("back-to-top", BackToTop);
export default {
  components: {
    Tab,
    Tabs,
    GoBack,
    ItemIconTooltip,
  },
  data () {
    return {
        result:null,
        loading:true,
        tabcache:[],
        noLink:false,
        category:[],
        defaultTab:"1",
        isHover:false,
        hoverIn: () => {},
        hoverOut: () => {},
        currentRowId:0,
        jobs:[],
        selectJob:23,
        currentTabId:1,
    }
  },
  created(){
      this.jobData();
  },
  mounted(){
    this.hoverIn = debounce(() => {
            this.isHover = true;
        }, 250);
    this.hoverOut = debounce(() => {
            this.isHover = false;
        }, 500);
  },
  computed:{
      GridTotal(){
          const total = this.result.row * 9;
          let gridItem = [];
          for (let i = 1; i <= total; i++) {
              gridItem[i-1] = this.result.list.find((r) => r.slot === i) || null;
          }
          return gridItem;
      },
      rowcontent(){
          let rowArray = [];
          this.result.list.forEach((v,k) => {
              rowArray[v.RowId] = v;
          });
        return rowArray;
      }
  },
  methods: {
        async getTabInfo(tabId){
            this.loading = true;
            this.currentTabId = tabId;
            const cacheKey = this.selectJob + '-' + tabId;
            if(this.tabcache[cacheKey]){
                this.result = this.tabcache[cacheKey];
                this.loading = false;
                this.category = this.tabcache[cacheKey].tabs;
            }else{
            await GameInfo.EquipmentBook(tabId,this.selectJob,this.$store.state.regionCode,
                (res) => {
                    if(tabId == 1){
                        this.$refs.eqbook.selectTab(0,tabId);
                    }
                    this.result = res;
                    this.tabcache[cacheKey] = res;
                    this.loading = false;
                    this.category = res.tabs;
                },
                (err) => {
                    this.error = err;
                    this.loading = false;
                });
            }
      },
        onHoverIn(id) {
            this.currentRowId = id;
            this.isHover = true;
        },
        onHoverOut() {
            this.isHover = false;
        },
        watchChange(e){
            this.selectJob = e.target.value;
            this.getTabInfo(this.currentTabId);
        },
        jobData(){
             GameInfo.getJobList(this.$store.state.regionCode,
                (res) => {
                    this.jobs = res;
                },
                (err) => {
                    this.error = err;
                    this.loading = false;
                });
        }
  },
}
</script>
<style lang="less" scoped>
@import "../../less/core.less";
.Title {
    display: flex;
    flex-direction: row;

    .titleitem {
        flex: 0 0 auto;
    }
}

.labryinth-table{
    margin:0 auto;
}
.topButton{
  width: 60px;
  height: 60px;
  padding: 10px 10px;
  //border-radius: 50%;
  font-size: 22px;
  //line-height: 22px;
  border:1px solid @dv-c-idle;
}
@tile-size: 58px;
.tab{
    padding:0px;
    .item-grid {
        display: inline-grid;
        grid-template-columns: @tile-size @tile-size @tile-size @tile-size @tile-size @tile-size @tile-size @tile-size @tile-size;
        border: 1px solid @dv-c-accent-1;
        padding: 1px;
        grid-gap: 2px;
        background: fade(@dv-c-accent-2, 20%);
        .grid-entry {
            width: @tile-size;
            height: @tile-size;
            background: fade(@dv-c-background, 80%);
            transition: background-color 0.125s ease-in;

            &:hover {
                background: fade(@dv-c-background, 40%);
            }
                    
            &.highlight {
                outline: 2px solid;
                outline-offset: -2px;
                animation: pulse 1s infinite;
                
                @keyframes pulse {
                    0% {
                        outline-color: @dv-c-idle;
                    }
                    50% {
                        outline-color: @dv-c-green;
                    }
                    100% {
                        outline-color: @dv-c-idle;
                    }
                }
            }
        }
    }
}
.rightPannel{
    
}
</style>

